* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #dedddd;
  font-family: Georgia, Times New Roman, Times, serif;
}

.heading {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.title {
  justify-content: center;
  display: flex;
}

.name {
  text-align: center;
  border-color: azure;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px;
  font-family: Georgia, Times New Roman, Times, serif;
  font-size: 20px;
}

.board {
  grid-gap: 10px;
  border: 2px solid #5f6165;
  border-radius: 10px;
  grid-template-rows: repeat(3, 100px);
  grid-template-columns: repeat(3, 100px);
  padding: 10px;
  display: grid;
}

.board__item {
  background-color: #b1c1de;
  border: 1px solid #5f6165;
  border-radius: 10px;
  position: relative;
}

.board__item:hover {
  cursor: pointer;
  background-color: #728cba;
  transition: all .2s ease-in-out;
}

.board__item--filled-x:after, .board__item--filled-x:before {
  height: 5px;
  width: 100%;
  content: "";
  background-color: red;
  position: absolute;
  top: 50%;
  left: 0;
}

.board__item--filled-x:before {
  transform: translateY(-50%)rotate(-45deg);
}

.board__item--filled-x:after {
  transform: translateY(50%)rotate(45deg);
}

.board__item--filled-o:before {
  content: "";
  width: 80%;
  height: 80%;
  border: 5px solid #4141ff;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wrapper {
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

.info {
  height: 50px;
  font-size: 30px;
  font-weight: 600;
}

.reset-btn {
  border: 1px solid #5f6165;
  border-radius: 10px;
  margin-top: 10px;
  padding: 10px 20px;
}

.reset-btn:hover {
  cursor: pointer;
  color: #fff;
  background-color: #728cba;
  transition: all .2s ease-in-out;
}

